// ==============================================
// RISIVO CRM - DNS Records API
// GET: List DNS records, POST: Add record
// ==============================================

import { NextRequest, NextResponse } from 'next/server'
import { prisma } from '@/lib/db'
import { createResellerClubClient } from '@/lib/email-infrastructure/resellerclub'

// Valid DNS record types
const VALID_TYPES = ['A', 'AAAA', 'CNAME', 'MX', 'TXT', 'NS', 'SRV']

// GET /api/email-infrastructure/dns
// List DNS records for a domain
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const domainId = searchParams.get('domainId')
    const type = searchParams.get('type')

    if (!domainId) {
      return NextResponse.json(
        { success: false, error: 'domainId is required' },
        { status: 400 }
      )
    }

    // Get domain
    const domain = await prisma.domain.findUnique({
      where: { id: domainId },
    })

    if (!domain) {
      return NextResponse.json(
        { success: false, error: 'Domain not found' },
        { status: 404 }
      )
    }

    // Build query
    const where: any = { domainId }
    if (type && type !== 'all') {
      where.type = type
    }

    // Get records from database
    const dnsRecords = await prisma.dnsRecord.findMany({
      where,
      orderBy: [
        { type: 'asc' },
        { name: 'asc' },
      ],
    })

    // Try to sync with ResellerClub
    const resellerClub = createResellerClubClient()
    if (resellerClub && domain.resellerClubDomainId) {
      try {
        const remoteRecords = await resellerClub.getDnsRecords(domain.domainName)
        // Note: In production, you'd sync these with local records
      } catch (error) {
        console.error('[API] Failed to fetch ResellerClub DNS records:', error)
      }
    }

    // Calculate stats
    const stats = {
      total: dnsRecords.length,
      byType: dnsRecords.reduce((acc, record) => {
        acc[record.type] = (acc[record.type] || 0) + 1
        return acc
      }, {} as Record<string, number>),
      systemManaged: dnsRecords.filter(r => r.isSystem).length,
      pendingSync: dnsRecords.filter(r => r.syncStatus === 'pending').length,
    }

    return NextResponse.json({
      success: true,
      data: dnsRecords,
      stats,
      domain: {
        id: domain.id,
        domainName: domain.domainName,
        spfConfigured: domain.spfConfigured,
        dkimConfigured: domain.dkimConfigured,
        dmarcConfigured: domain.dmarcConfigured,
      },
    })
  } catch (error) {
    console.error('[API] List DNS records error:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to fetch DNS records' },
      { status: 500 }
    )
  }
}

// POST /api/email-infrastructure/dns
// Add a new DNS record
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const {
      domainId,
      type,
      name,
      value,
      ttl = 3600,
      priority,
    } = body

    // Validate required fields
    if (!domainId || !type || !name || !value) {
      return NextResponse.json(
        { success: false, error: 'domainId, type, name, and value are required' },
        { status: 400 }
      )
    }

    // Validate type
    if (!VALID_TYPES.includes(type)) {
      return NextResponse.json(
        { success: false, error: `Invalid record type. Must be one of: ${VALID_TYPES.join(', ')}` },
        { status: 400 }
      )
    }

    // Validate MX priority
    if (type === 'MX' && (priority === undefined || priority === null)) {
      return NextResponse.json(
        { success: false, error: 'Priority is required for MX records' },
        { status: 400 }
      )
    }

    // Validate TTL
    if (ttl < 60 || ttl > 86400) {
      return NextResponse.json(
        { success: false, error: 'TTL must be between 60 and 86400 seconds' },
        { status: 400 }
      )
    }

    // Get domain
    const domain = await prisma.domain.findUnique({
      where: { id: domainId },
    })

    if (!domain) {
      return NextResponse.json(
        { success: false, error: 'Domain not found' },
        { status: 404 }
      )
    }

    // Check for duplicate record
    const existingRecord = await prisma.dnsRecord.findFirst({
      where: {
        domainId,
        type,
        name,
        value,
      },
    })

    if (existingRecord) {
      return NextResponse.json(
        { success: false, error: 'A DNS record with the same type, name, and value already exists' },
        { status: 400 }
      )
    }

    // Add to ResellerClub if configured
    const resellerClub = createResellerClubClient()
    let syncStatus = 'pending'
    let resellerClubRecordId: string | null = null

    if (resellerClub && domain.resellerClubDomainId) {
      try {
        const success = await resellerClub.addDnsRecord(domain.domainName, {
          host: name,
          type: type as any,
          value,
          ttl,
          priority,
        })

        if (success) {
          syncStatus = 'synced'
          resellerClubRecordId = `record-${Date.now()}`
        }
      } catch (error) {
        console.error('[API] Failed to add ResellerClub DNS record:', error)
        syncStatus = 'failed'
      }
    }

    // Create DNS record
    const dnsRecord = await prisma.dnsRecord.create({
      data: {
        domainId,
        type,
        name,
        value,
        ttl,
        priority: type === 'MX' ? priority : null,
        isSystem: false,
        isEditable: true,
        resellerClubRecordId,
        syncStatus,
        lastSyncAt: syncStatus === 'synced' ? new Date() : null,
      },
    })

    // Check if this affects deliverability status
    let deliverabilityUpdate: any = {}
    
    if (type === 'TXT') {
      if (value.includes('v=spf1')) {
        deliverabilityUpdate.spfConfigured = true
      }
      if (value.includes('v=DKIM1')) {
        deliverabilityUpdate.dkimConfigured = true
      }
      if (value.includes('v=DMARC1')) {
        deliverabilityUpdate.dmarcConfigured = true
      }
    }

    if (Object.keys(deliverabilityUpdate).length > 0) {
      await prisma.domain.update({
        where: { id: domainId },
        data: deliverabilityUpdate,
      })
    }

    return NextResponse.json({
      success: true,
      data: dnsRecord,
      message: 'DNS record added successfully',
      syncStatus,
    })
  } catch (error) {
    console.error('[API] Add DNS record error:', error)
    return NextResponse.json(
      { success: false, error: 'Failed to add DNS record' },
      { status: 500 }
    )
  }
}
