// ==============================================
// RISIVO CRM - ResellerClub API Integration
// Domain registration, email hosting, DNS management
// API Documentation: https://manage.resellerclub.com/kb/answer/744
// ==============================================

export interface ResellerClubConfig {
  authUserId: string
  apiKey: string
  testMode?: boolean // Use test API endpoint
}

export interface DomainAvailability {
  domain: string
  status: 'available' | 'unavailable' | 'error'
  price?: number
  currency?: string
}

export interface DomainRegistrationRequest {
  domainName: string
  years: number
  customerId: string
  registrantContact: ContactDetails
  adminContact: ContactDetails
  techContact: ContactDetails
  billingContact: ContactDetails
  nameservers?: string[]
  privacyProtection?: boolean
}

export interface ContactDetails {
  name: string
  company?: string
  email: string
  addressLine1: string
  addressLine2?: string
  city: string
  state: string
  country: string
  zipCode: string
  phone: string
  phoneCC: string // Country code
}

export interface DomainInfo {
  domainId: string
  domainName: string
  status: string
  registrationDate: string
  expirationDate: string
  nameservers: string[]
  orderId: string
  customerId: string
  autoRenew: boolean
}

export interface DnsRecord {
  host: string
  type: 'A' | 'AAAA' | 'CNAME' | 'MX' | 'TXT' | 'NS' | 'SRV'
  value: string
  ttl: number
  priority?: number
}

export interface EmailAccountRequest {
  email: string
  password: string
  firstName?: string
  lastName?: string
  storageQuota?: number // MB
}

/**
 * ResellerClub API Client
 * Handles all domain and email hosting operations
 */
export class ResellerClubClient {
  private baseUrl: string
  private authUserId: string
  private apiKey: string

  constructor(config: ResellerClubConfig) {
    this.baseUrl = config.testMode
      ? 'https://test.httpapi.com/api'
      : 'https://httpapi.com/api'
    this.authUserId = config.authUserId
    this.apiKey = config.apiKey
  }

  /**
   * Build API request URL with authentication
   */
  private buildUrl(endpoint: string, params: Record<string, string | number | boolean> = {}): string {
    const url = new URL(`${this.baseUrl}${endpoint}`)
    url.searchParams.set('auth-userid', this.authUserId)
    url.searchParams.set('api-key', this.apiKey)
    
    Object.entries(params).forEach(([key, value]) => {
      url.searchParams.set(key, String(value))
    })
    
    return url.toString()
  }

  /**
   * Make API request
   */
  private async request<T>(
    endpoint: string,
    method: 'GET' | 'POST' = 'GET',
    params: Record<string, any> = {}
  ): Promise<T> {
    const url = method === 'GET'
      ? this.buildUrl(endpoint, params)
      : this.buildUrl(endpoint)

    const options: RequestInit = {
      method,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
    }

    if (method === 'POST') {
      const formData = new URLSearchParams()
      Object.entries(params).forEach(([key, value]) => {
        formData.append(key, String(value))
      })
      options.body = formData.toString()
    }

    const response = await fetch(url, options)
    const data = await response.json()

    if (data.status === 'ERROR') {
      throw new Error(data.message || 'ResellerClub API error')
    }

    return data
  }

  // ==============================================
  // DOMAIN OPERATIONS
  // ==============================================

  /**
   * Check domain availability
   */
  async checkDomainAvailability(
    domain: string,
    tlds: string[] = ['com', 'net', 'org', 'io']
  ): Promise<DomainAvailability[]> {
    const domainName = domain.split('.')[0]
    
    const params: Record<string, string> = {
      'domain-name': domainName,
    }
    
    tlds.forEach((tld, index) => {
      params[`tlds[${index}]`] = tld
    })

    try {
      const response = await this.request<Record<string, { status: string }>>('/domains/available.json', 'GET', params)
      
      return Object.entries(response).map(([fullDomain, data]) => ({
        domain: fullDomain,
        status: data.status === 'available' ? 'available' : 'unavailable',
      }))
    } catch (error) {
      console.error('[ResellerClub] Domain availability check failed:', error)
      return tlds.map(tld => ({
        domain: `${domainName}.${tld}`,
        status: 'error',
      }))
    }
  }

  /**
   * Get domain pricing for TLDs
   */
  async getDomainPricing(tlds: string[]): Promise<Record<string, { register: number; renew: number; transfer: number }>> {
    // Note: This would typically call the pricing API
    // For now, return mock pricing
    const mockPricing: Record<string, { register: number; renew: number; transfer: number }> = {
      com: { register: 10.99, renew: 12.99, transfer: 10.99 },
      net: { register: 9.99, renew: 11.99, transfer: 9.99 },
      org: { register: 8.99, renew: 10.99, transfer: 8.99 },
      io: { register: 32.99, renew: 39.99, transfer: 32.99 },
      co: { register: 24.99, renew: 29.99, transfer: 24.99 },
      ai: { register: 69.99, renew: 79.99, transfer: 69.99 },
    }

    const result: Record<string, { register: number; renew: number; transfer: number }> = {}
    tlds.forEach(tld => {
      result[tld] = mockPricing[tld] || { register: 15.99, renew: 17.99, transfer: 15.99 }
    })

    return result
  }

  /**
   * Register a new domain
   */
  async registerDomain(request: DomainRegistrationRequest): Promise<{ orderId: string; domainId: string }> {
    const params: Record<string, any> = {
      'domain-name': request.domainName,
      years: request.years,
      'customer-id': request.customerId,
      'reg-contact-id': await this.getOrCreateContact(request.registrantContact),
      'admin-contact-id': await this.getOrCreateContact(request.adminContact),
      'tech-contact-id': await this.getOrCreateContact(request.techContact),
      'billing-contact-id': await this.getOrCreateContact(request.billingContact),
      'invoice-option': 'NoInvoice',
      'protect-privacy': request.privacyProtection || false,
    }

    if (request.nameservers && request.nameservers.length > 0) {
      request.nameservers.forEach((ns, index) => {
        params[`ns${index + 1}`] = ns
      })
    }

    try {
      const response = await this.request<{ entityid: string; actiontypedesc: string }>(
        '/domains/register.json',
        'POST',
        params
      )

      return {
        orderId: response.entityid,
        domainId: response.entityid,
      }
    } catch (error) {
      console.error('[ResellerClub] Domain registration failed:', error)
      throw error
    }
  }

  /**
   * Get domain details
   */
  async getDomainDetails(domainName: string): Promise<DomainInfo | null> {
    try {
      const response = await this.request<any>('/domains/details-by-name.json', 'GET', {
        'domain-name': domainName,
        options: 'All',
      })

      return {
        domainId: response.entityid,
        domainName: response.domainname,
        status: response.currentstatus,
        registrationDate: response.creationdt,
        expirationDate: response.endtime,
        nameservers: [response.ns1, response.ns2, response.ns3, response.ns4].filter(Boolean),
        orderId: response.orderid,
        customerId: response.customerid,
        autoRenew: response.autorenew === 'true',
      }
    } catch (error) {
      console.error('[ResellerClub] Get domain details failed:', error)
      return null
    }
  }

  /**
   * Renew domain
   */
  async renewDomain(domainName: string, years: number = 1): Promise<boolean> {
    try {
      const domain = await this.getDomainDetails(domainName)
      if (!domain) throw new Error('Domain not found')

      await this.request('/domains/renew.json', 'POST', {
        'order-id': domain.orderId,
        years,
        'exp-date': domain.expirationDate,
        'invoice-option': 'NoInvoice',
      })

      return true
    } catch (error) {
      console.error('[ResellerClub] Domain renewal failed:', error)
      return false
    }
  }

  /**
   * Set domain nameservers
   */
  async setNameservers(domainName: string, nameservers: string[]): Promise<boolean> {
    try {
      const domain = await this.getDomainDetails(domainName)
      if (!domain) throw new Error('Domain not found')

      const params: Record<string, string> = {
        'order-id': domain.orderId,
      }

      nameservers.forEach((ns, index) => {
        params[`ns${index + 1}`] = ns
      })

      await this.request('/domains/modify-ns.json', 'POST', params)
      return true
    } catch (error) {
      console.error('[ResellerClub] Set nameservers failed:', error)
      return false
    }
  }

  // ==============================================
  // DNS MANAGEMENT
  // ==============================================

  /**
   * Add DNS record
   */
  async addDnsRecord(domainName: string, record: DnsRecord): Promise<boolean> {
    try {
      const endpoint = `/dns/manage/add-${record.type.toLowerCase()}-record.json`
      
      const params: Record<string, any> = {
        'domain-name': domainName,
        host: record.host,
        value: record.value,
        ttl: record.ttl,
      }

      if (record.type === 'MX' && record.priority !== undefined) {
        params.priority = record.priority
      }

      await this.request(endpoint, 'POST', params)
      return true
    } catch (error) {
      console.error('[ResellerClub] Add DNS record failed:', error)
      return false
    }
  }

  /**
   * Delete DNS record
   */
  async deleteDnsRecord(domainName: string, host: string, type: string, value: string): Promise<boolean> {
    try {
      await this.request(`/dns/manage/delete-${type.toLowerCase()}-record.json`, 'POST', {
        'domain-name': domainName,
        host,
        value,
      })
      return true
    } catch (error) {
      console.error('[ResellerClub] Delete DNS record failed:', error)
      return false
    }
  }

  /**
   * Get all DNS records for a domain
   */
  async getDnsRecords(domainName: string): Promise<DnsRecord[]> {
    try {
      const response = await this.request<any>('/dns/manage/search-records.json', 'GET', {
        'domain-name': domainName,
        'no-of-records': 50,
        'page-no': 1,
      })

      if (!response.records) return []

      return response.records.map((r: any) => ({
        host: r.host,
        type: r.type,
        value: r.value,
        ttl: parseInt(r.ttl),
        priority: r.priority ? parseInt(r.priority) : undefined,
      }))
    } catch (error) {
      console.error('[ResellerClub] Get DNS records failed:', error)
      return []
    }
  }

  /**
   * Set up email deliverability DNS records (SPF, DKIM, DMARC)
   */
  async setupEmailDns(domainName: string, options: {
    spfRecord?: string
    dkimSelector?: string
    dkimPublicKey?: string
    dmarcPolicy?: 'none' | 'quarantine' | 'reject'
    dmarcEmail?: string
  } = {}): Promise<{ spf: boolean; dkim: boolean; dmarc: boolean }> {
    const results = { spf: false, dkim: false, dmarc: false }

    // SPF Record
    const spfValue = options.spfRecord || 'v=spf1 include:_spf.google.com ~all'
    results.spf = await this.addDnsRecord(domainName, {
      host: '@',
      type: 'TXT',
      value: spfValue,
      ttl: 3600,
    })

    // DKIM Record
    if (options.dkimSelector && options.dkimPublicKey) {
      results.dkim = await this.addDnsRecord(domainName, {
        host: `${options.dkimSelector}._domainkey`,
        type: 'TXT',
        value: `v=DKIM1; k=rsa; p=${options.dkimPublicKey}`,
        ttl: 3600,
      })
    }

    // DMARC Record
    const dmarcPolicy = options.dmarcPolicy || 'quarantine'
    const dmarcEmail = options.dmarcEmail || `dmarc@${domainName}`
    results.dmarc = await this.addDnsRecord(domainName, {
      host: '_dmarc',
      type: 'TXT',
      value: `v=DMARC1; p=${dmarcPolicy}; rua=mailto:${dmarcEmail}`,
      ttl: 3600,
    })

    return results
  }

  // ==============================================
  // EMAIL HOSTING (via ResellerClub Email)
  // ==============================================

  /**
   * Create email account
   */
  async createEmailAccount(domainName: string, request: EmailAccountRequest): Promise<{ emailId: string } | null> {
    try {
      const response = await this.request<{ entityid: string }>('/mail/user/add.json', 'POST', {
        'domain-name': domainName,
        email: request.email,
        passwd: request.password,
        'first-name': request.firstName || 'User',
        'last-name': request.lastName || '',
        'quota': request.storageQuota || 5120, // 5GB default
      })

      return { emailId: response.entityid }
    } catch (error) {
      console.error('[ResellerClub] Create email account failed:', error)
      return null
    }
  }

  /**
   * Delete email account
   */
  async deleteEmailAccount(domainName: string, email: string): Promise<boolean> {
    try {
      await this.request('/mail/user/delete.json', 'POST', {
        'domain-name': domainName,
        email,
      })
      return true
    } catch (error) {
      console.error('[ResellerClub] Delete email account failed:', error)
      return false
    }
  }

  /**
   * Update email account password
   */
  async updateEmailPassword(domainName: string, email: string, newPassword: string): Promise<boolean> {
    try {
      await this.request('/mail/user/modify.json', 'POST', {
        'domain-name': domainName,
        email,
        passwd: newPassword,
      })
      return true
    } catch (error) {
      console.error('[ResellerClub] Update email password failed:', error)
      return false
    }
  }

  /**
   * Get email account details
   */
  async getEmailAccount(domainName: string, email: string): Promise<any> {
    try {
      const response = await this.request('/mail/user/details.json', 'GET', {
        'domain-name': domainName,
        email,
      })
      return response
    } catch (error) {
      console.error('[ResellerClub] Get email account failed:', error)
      return null
    }
  }

  /**
   * List all email accounts for a domain
   */
  async listEmailAccounts(domainName: string): Promise<any[]> {
    try {
      const response = await this.request<{ users: any[] }>('/mail/user/search.json', 'GET', {
        'domain-name': domainName,
        'no-of-records': 100,
        'page-no': 1,
      })
      return response.users || []
    } catch (error) {
      console.error('[ResellerClub] List email accounts failed:', error)
      return []
    }
  }

  // ==============================================
  // CUSTOMER MANAGEMENT
  // ==============================================

  /**
   * Create or get contact ID
   */
  private async getOrCreateContact(contact: ContactDetails): Promise<string> {
    // In production, you would:
    // 1. Search for existing contact
    // 2. Create new if not found
    // 3. Return contact ID
    
    // For now, return a mock ID
    return 'contact-' + Date.now()
  }

  /**
   * Create customer account
   */
  async createCustomer(details: {
    email: string
    password: string
    name: string
    company?: string
    phone: string
    phoneCC: string
    address: string
    city: string
    state: string
    country: string
    zipCode: string
  }): Promise<{ customerId: string } | null> {
    try {
      const response = await this.request<{ entityid: string }>('/customers/v2/signup.json', 'POST', {
        username: details.email,
        passwd: details.password,
        name: details.name,
        company: details.company || details.name,
        'address-line-1': details.address,
        city: details.city,
        state: details.state,
        country: details.country,
        zipcode: details.zipCode,
        'phone-cc': details.phoneCC,
        phone: details.phone,
        'lang-pref': 'en',
      })

      return { customerId: response.entityid }
    } catch (error) {
      console.error('[ResellerClub] Create customer failed:', error)
      return null
    }
  }
}

/**
 * Create ResellerClub client from environment variables
 */
export function createResellerClubClient(): ResellerClubClient | null {
  const authUserId = process.env.RESELLERCLUB_AUTH_USER_ID
  const apiKey = process.env.RESELLERCLUB_API_KEY
  const testMode = process.env.RESELLERCLUB_TEST_MODE === 'true'

  if (!authUserId || !apiKey) {
    console.warn('[ResellerClub] Missing credentials, client not initialized')
    return null
  }

  return new ResellerClubClient({
    authUserId,
    apiKey,
    testMode,
  })
}

export default ResellerClubClient
